/* Lights */
.lights {
  position: fixed;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

.lights__group {
  position: relative;
  height: 100%;
}

.lights__light {
  --size: 0.35rem;
  width: var(--size);
  height: var(--size);
  position: absolute;
  background: white;
  border-radius: 100%;
  top: 10%;
  left: 25%;
  filter: blur(0.1rem);
  animation: blink 2500ms var(--d, 0ms) infinite alternate;
}
.lights__light:nth-child(2) {
  --d: 200ms;
  top: 40%;
  left: 12%;
}
.lights__light:nth-child(3) {
  --d: 350ms;
  top: 60%;
  left: 18%;
}
.lights__light:nth-child(4) {
  --d: 600ms;
  top: 25%;
  left: 66%;
}
.lights__light:nth-child(5) {
  --d: 1210ms;
  top: 43%;
  left: 55%;
}
.lights__light:nth-child(6) {
  --d: 420ms;
  top: 90%;
  left: 37%;
}
.lights__light:nth-child(7) {
  --d: 1100ms;
  top: 82%;
  left: 91%;
}
.lights__light:nth-child(8) {
  --d: 1560ms;
  top: 67%;
  left: 81%;
}

@keyframes blink {
  to {
    opacity: 0;
  }
}

