/* custom input */

.form {
  width: 100%;
  position: relative;
  margin-top: 25px;
  border: none;
  height: 50px;
}

.textarea {
  height: 150px;
}

.form input,
.form textarea {
  width: 100%;
  height: 100%;
  color: #fff;
  padding-top: 10px;
  font-size: large;
  border: none;
  background-color: transparent;
}
.form textarea {
  height: auto;
  margin-top: 50px;
}

.form label {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-bottom: 1px solid white;
  border-bottom: 1px solid white;
  overflow: hidden;
}

.form label::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0px;
  width: 100%;
  height: 100%;
  border-bottom: 3px solid #fce38a;
  transform: translateX(-100%);
  transition: all 0.3s ease;
}

.content-name {
  position: absolute;
  bottom: 10px;
  left: 0px;
  transition: all 0.3s ease;
}
.textarea .content-name {
  top: 25px;
  bottom: auto;
}

.form input:focus {
  outline: none;
}
.form textarea:focus {
  outline: none;
}

.form input:focus + .label-name .content-name,
.form input:valid + .label-name .content-name {
  transform: translateY(-150%);
  font-size: 14px;
  left: 0px;
  color: #fce38a;
}
.form input:focus + .label-name::after,
.form input:valid + .label-name::after {
  transform: translateX(0%);
}

.form textarea:focus + .label-name .content-name,
.form textarea:valid + .label-name .content-name {
  left: 0px;
  color: #fce38a;
}
.form textarea:focus + .label-name::after,
.form textarea:valid + .label-name::after {
  transform: translateX(0%);
}

/* custom input */

/* custom checkbox */
input[type="checkbox"] {
  height: 0px;
  width: 0px;
  position: absolute;
  visibility: hidden;
}

input[type="checkbox"] + label {
  position: relative;
  display: flex;
  margin: 0.6em 0;
  align-items: center;
  color: #e7e7e7;
  transition: color 250ms cubic-bezier(0.4, 0, 0.23, 1);
}
input[type="checkbox"] + label > ins {
  position: absolute;
  display: block;
  bottom: 0;
  left: 2em;
  height: 0;
  width: 100%;
  overflow: hidden;
  text-decoration: none;
  transition: height 300ms cubic-bezier(0.4, 0, 0.23, 1);
}
input[type="checkbox"] + label > ins > i {
  position: absolute;
  bottom: 0;
  font-style: normal;
  color: #fce38a;
}
input[type="checkbox"] + label > span {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1em;
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  user-select: none;
  background: transparent;
  border: 2px solid #9e9e9e;
  border-radius: 2px;
  cursor: pointer;
  transition: all 250ms cubic-bezier(0.4, 0, 0.23, 1);
}

input[type="checkbox"] + label:hover,
input[type="checkbox"]:focus + label {
  color: #fff;
}
input[type="checkbox"] + label:hover > span,
input[type="checkbox"]:focus + label > span {
  background: rgba(255, 255, 255, 0.1);
}
input[type="checkbox"]:checked + label > ins {
  height: 100%;
}

input[type="checkbox"]:checked + label > span {
  border: 0.5em solid #fce38a;
  animation: shrink-bounce 200ms cubic-bezier(0.4, 0, 0.23, 1);
}
input[type="checkbox"]:checked + label > span:before {
  content: "";
  position: absolute;
  top: 40%;
  left: 0.1rem;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  transform: rotate(45deg);
  transform-origin: 0% 100%;
  animation: checkbox-check 125ms 250ms cubic-bezier(0.4, 0, 0.23, 1) forwards;
}

@keyframes shrink-bounce {
  0% {
    transform: scale(1);
  }
  33% {
    transform: scale(0.85);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes checkbox-check {
  0% {
    width: 0;
    height: 0;
    border-color: #212121;
    transform: translate3d(0, 0, 0) rotate(45deg);
  }
  33% {
    width: 0.2em;
    height: 0;
    transform: translate3d(0, 0, 0) rotate(45deg);
  }
  100% {
    width: 0.2em;
    height: 0.5em;
    border-color: #212121;
    transform: translate3d(0, -0.5em, 0) rotate(45deg);
  }
}

